<template>
  <div class="Home">
   	<div class="content">
   		 <ul class="leftList">
  	    	<li v-for="(singList,index) in leftList" :class="{'active':index == activedIndex}" @click="changeIndex(index)">{{singList.name}}</li>
  	    </ul>
          <div class="rightLists" @scroll="listScroll($event)" ref="rightLists">
              <div class="sectionList" v-for="(singList,index) in leftList" ref="lists">
                <dl>{{singList.name}}</dl> 
                <dt v-for="item in singList.foods" class="smallList">
                    <p>{{item.name}}</p>
                    <p>{{item.description}}</p>
                </dt>
              </div>  
           
          </div>
  	    
   	</div>
   
  </div>
</template>

<script>
import leftList from '../data/foodsJson.js'
export default {

  name: 'Home',
  data () {
    return {
      leftList,
      activedIndex:0, //被选中下标
      listHeight:[],   //列表高度集合数组，
    }
  },
  methods:{
    changeIndex:function(index){
      //选中下标
      this.activedIndex = index;
      //右侧对应被选中的列表项
      var sectionList = this.$refs.lists[index];
      //右侧容器节点
      var rightLists = this.$refs.rightLists; 
      // console.log(rightLists.offsetTop);
      clearInterval(scrollTimer);     
      //滑动动画
      var scrollTimer = setInterval(function(){
        if (rightLists.scrollTop + rightLists.offsetTop <= sectionList.offsetTop) {
            rightLists.scrollTop += 5;
            if (rightLists.scrollTop + rightLists.offsetTop >= sectionList.offsetTop || rightLists.scrollTop + rightLists.clientHeight >= rightLists.scrollHeight) {
                clearInterval(scrollTimer);
             }
        }else{
          if (rightLists.scrollTop + rightLists.offsetTop <= sectionList.offsetTop) {
              clearInterval(scrollTimer);
          }
          rightLists.scrollTop -= 5; 
        }
      },1);

    },
    listScroll(event){
        this.listHeightAll;
        //滚动距离
        var scrollValue = event.srcElement.scrollTop;
        var length = this.listHeight.length;
        // console.log(this.listHeight);
        // console.log(length);
        for(let i = 0;i<length-1;i++){
            let height1 = this.listHeight[i];
            let height2 = this.listHeight[i+1];
            if(!height2 || (scrollValue >= height1 && scrollValue < height2)){
              this.activedIndex = i;
            }
        }  
    }
  },
  computed:{
    listHeightAll(){
      //右侧列表dom对象集合
      var foodList = this.$refs.lists;
      var height = 0;
      this.listHeight.push(height);
      for(let i = 0;i < foodList.length;i++){
          let itemList = foodList[i];
          height += itemList.offsetHeight;
          this.listHeight.push(height);
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .content{
    display: flex;
  }
  .leftList{
    margin-right: 0.25rem;
  }
  .leftList li{
  	width:5rem;
  	height:2rem;
  	line-height: 2rem;
    text-align: center;
    margin:.25rem;
  	border:1px solid #ccc;
  }
  .active{
    background-color: #e8e8e8;
  }

  .rightLists{
    height:20rem;
    overflow-y: scroll;
  }
  .sectionList{
    width:10rem;
    font-size: .8rem;
  }
  .sectionList dl{
    height:2rem;
    line-height: 2rem;
    background-color: skyblue;
  }
  .smallList{
    height:4rem;
    border:1px solid red;
  }


</style>



